.catalog{
  padding: 1em 0 3.125em;
  &--category{
    padding-top: 4em;
  }
  &__content{
    margin-top: 2.5em;
  }
  &__cards{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 8.0625em / 4));
    gap: 2.6875em;
  }
}

.catalog-top{
  display: flex;
  justify-content: space-between;
  &__col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .line-btn{
    margin-top: .5em;
  }
  &__count{
    font-family: $font2;
    font-size: .9375em;
    line-height: 307%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }
}

.line-btn{
  width: 13.3125em;
  height: 1.5625em;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-weight: 700;
  color: #a6a6a6;
  font-family: $font2;
  &__bg{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
  &__icon{
    width: .9375em;
    margin-right: .4375em;
  }
  span{
    font-size: .625em;
  }
  &:hover{
    color: #d8d8d8;
    .line-btn__bg path{
      stroke: #d8d8d8;
    }
    .line-btn__icon path{
      fill: #d8d8d8;
    }
  }
  &:active{
    color: $primary;
    .line-btn__bg path{
      stroke: $primary;
    }
    .line-btn__icon path{
      fill: $primary;
    }
  }
}

.catalog-view{
  display: flex;
  align-items: center;
  margin-left: 1.5em;
  &__tab{
    height: 1.3125em;
    margin-right: .875em;
    cursor: pointer;
    &:last-child{
      margin-right: 0;
    }
    svg{
      height: 100%;
      width: auto;
    }
    &:hover,
    &._active{
      rect{
        fill: $primary;
      }
    }
  }
}

