}

.more-btn{
  position: relative;
  height: 2em;
  width: 35.25em;
  max-width: 100%;
  margin: 1.75em auto 0;
  @include center;
  color: #b7b7b7;
  font-family: $font2;
  font-weight: 700;
  text-transform: uppercase;
  overflow: hidden;
  span{
    font-size: .625em;
  }
  &__bg{
    height: 1em;
    width: auto;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0;
  }
  &:hover{
    color: #e2e2e2;
  }
  &:active{
    color: $primary;
  }
}


@media screen and (max-width: 992px){
  .catalog{
    padding: .8125em 0 3.5em;
    &__content{
      margin-top: .8125em;
    }
    &__cards{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .9375em / 2));
      gap: .9375em;
    }
  }

  .catalog-top{
    align-items: flex-start;
    &__col{
      max-width: 16em;
    }
    .line-btn{
      margin-top: 1.25em;
    }
  }

  .catalog-view{
    &__tab{
      height: 1.4375em;
      margin-right: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog{
    &__cards{
      grid-template-columns: 100%;
    }
  }

  .catalog-top{
    display: block;
    &__col2{
      margin-top: 1.25em;
      justify-content: space-between;
    }
    &__count{
      font-size: .8125em;
      line-height: 354%;
    }
  }
}
