    color: #a6a6a6;
    font-weight: 700;
    font-family: $font2;
    span{
      font-size: .75em;
    }
  }
  &__title{
    margin: 2em 0;
    text-align: center;
    .h2{
      line-height: 144%;
      font-weight: 700;
    }
  }
}

.blog-card2{
  padding: 1em 1.375em 1.375em;
  clip-path: polygon(0 100%,100% 100%,100% 1.25em,calc(100% - 1.875em) 0,0 0,0 100%);
  .blog-card{
    &__img{
      clip-path: polygon(0 100%,100% 100%,100% 1.125em,calc(100% - 1.625em) 0,0 0,0 100%);
      padding-bottom: 48%;
    }
    &__title{
      font-size: 1em;
      line-height: 137%;
    }
  }
  .m-badge{
    min-width: 7em;
    justify-content: center;
  }
  .product-card2__nav{
    width: 100%;
    padding-top: 1.25em;
  }
}

.text{
  ul,
  p{
    @include lastM(0 0 1.5em);
  }
  h5{
    font-weight: 500;
    font-size: 1.125em;
    line-height: 139%;
    margin-bottom: 1.83em;
  }
}

.equipment-slider,
.news-gallery,
.categories{
  .swiper-pagination{
    margin: 0 1em;
    font-size: .8125em;
    font-family: $font2;
    line-height: 180%;
    letter-spacing: 0.4em;
    text-transform: uppercase;
    ._current{
      font-size: 1.53em;
      color: $primary;
      letter-spacing: 0.02em
    }
  }
}

.news-gallery{
  margin-top: 2.1875em;
  &__title{
    font-size: 1.25em;
    line-height: 180%;
    font-family: $font2;
    letter-spacing: 0.02em;
    text-transform: uppercase;
  }
  &__slider{
    margin-top: 2em;
    .swiper-slide{
      width: 31.625em;
      padding-right: 2em;
    }
  }
}

