    margin-bottom: .8125em;
  }
  &__title{
    font-size: .875em;
    line-height: 150%;
  }
}

.collection3{
  margin-top: 1.5em;
  padding-top: 3em;
  padding-bottom: 3.125em;
  background-image: url(../img/line2.svg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: auto 51px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 51px;
    border-bottom: 1px solid #ccc;
    @media screen and (max-width: 1440px){
      display: none;
    }
  }
  @media screen and (min-width: 1441px){
    padding-top: 6em;
  }
}


@media screen and (max-width: 992px){
  .selection3{
    padding: 2.25em 0;
  }

  .news{
    padding: .8125em 0 2.375em;
    &__grid{
      margin-top: .625em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
    &__preview{
      clip-path: polygon( 100% 99.826%,65.958% 99.826%,62.167% 96.426%,37.292% 96.426%,32.979% 99.826%,0% 99.826%,0% -0%,100% -0%,100% 99.826% );
      img{
        border-radius: .625em;
        min-height: 7.875em;
        object-fit: cover;
      }
    }
    &__title{
      margin: 1.25em 0 1.5em;
      .h2{
        font-size: 1.125em;
        line-height: 167%;
      }
    }
  }

  .text{
    font-size: .875em;
  }

  .news-gallery{
    margin-top: 1.125em;
    &__title{
      font-size: 1.125em;
      line-height: 189%;
    }
    .swiper-slide{
      width: 20.75em;
      padding-right: 1.375em;
    }
  }
}


@media screen and (max-width: 576px){
  .news{
    &__grid{
      grid-template-columns: 100%;
    }
    &__date{
      span{
        font-size: .6875em;
      }
    }
  }

  .news-gallery{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    .swiper-nav{
      margin-top: 1em;
    }
  }

  .collection3{
    background-image: url(../img/line3.svg);
    background-size: 100% auto;
    padding: 4em 0 4.75em;
    margin-top: 1.5em;
  }
}
