.selection4{
  padding: 2.625em 0;
  .selection__inner{
    background-image: url(../img/selection-bg6.webp);
  }
}

.equipment{
  padding: 7em 0 9.375em;
  background-color: $gray;
  background-image: url(../img/equipment-decor.svg), url(../img/equipment-decor2.svg);
  background-repeat: no-repeat;
  background-position: left top, left bottom;
  background-size: 100% auto;
  &__grid{
    position: relative;
    min-height: 23.75em;
  }
  &__content{
    max-width: 40em;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    padding-right: 1.25em;
    padding-bottom: 1.5em;
    width: 33em;
    background-image: url(../img/rect.svg);
    background-size: calc(100% - 1.25em) auto;
    background-repeat: no-repeat;
    background-position: right bottom;
    &-wrap{
      clip-path: polygon( 99.751% 86.626%,99.751% 86.626%,99.797% 86.547%,99.839% 86.464%,99.876% 86.377%,99.908% 86.287%,99.936% 86.193%,99.959% 86.097%,99.977% 85.999%,99.99% 85.899%,99.997% 85.798%,100% 85.695%,100% 1.409%,100% 1.409%,99.987% 1.181%,99.95% 0.966%,99.89% 0.765%,99.81% 0.581%,99.712% 0.418%,99.597% 0.278%,99.468% 0.164%,99.327% 0.079%,99.175% 0.026%,99.016% 0.008%,7.886% 0.008%,7.886% 0.008%,7.808% 0.013%,7.73% 0.026%,7.653% 0.048%,7.578% 0.078%,7.506% 0.117%,7.435% 0.164%,7.367% 0.219%,7.303% 0.281%,7.241% 0.351%,7.184% 0.428%,0.282% 10.439%,0.282% 10.439%,0.23% 10.52%,0.183% 10.606%,0.141% 10.697%,0.105% 10.791%,0.073% 10.89%,0.047% 10.991%,0.027% 11.095%,0.012% 11.202%,0.003% 11.31%,0% 11.42%,0% 98.327%,0% 98.327%,0.013% 98.555%,0.05% 98.77%,0.11% 98.971%,0.19% 99.155%,0.288% 99.318%,0.403% 99.458%,0.532% 99.572%,0.673% 99.657%,0.825% 99.71%,0.984% 99.728%,91.119% 99.728%,91.119% 99.728%,91.203% 99.723%,91.286% 99.708%,91.367% 99.683%,91.446% 99.649%,91.522% 99.605%,91.596% 99.553%,91.667% 99.492%,91.734% 99.422%,91.796% 99.344%,91.855% 99.258%,99.751% 86.626% );
      img{
        border-radius: .5625em;
        width: 100%;
        display: block;
      }
    }
  }
  &__desc{
    margin-top: 1.125em;
    max-width: 31.25em;
  }
  &__subtitle{
    margin-top: 1em;
    text-transform: uppercase;
  }
  .service__list{
    margin-top: 1.125em;
  }
  &__nav{
    display: flex;
    margin-top: 2em;
  }
}

.service-center{
  padding: .3125em 0 1.25em;
  &__top{
    @include grid100gap(.8125em);
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
    margin-top: 2.5em;
  }
  .map{
    margin-top: 2em;
  }
}

.region-select{
  margin-top: 1.25em;
  select{
    border: none;
    width: 100%;
    height: 4em;
    font-size: .625em;
    padding-right: 4.5em;
    padding-left: .25em;
    color: #a6a6a6;
    font-weight: 700;
    font-family: $font2;
    background-color: transparent !important;
    background-image: url(../img/line4.svg),url(../img/arrow.svg);
    background-repeat: no-repeat;
    background-position: right bottom, right 2.4em center;
    background-size: auto 1.6em, 1.125em auto;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }
  }
}

.center-card{
  background-image: url(../img/center-card-stroke.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  position: relative;
  padding-bottom: 40.82%;
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1.375em;
  }
  &__icon{
    filter: drop-shadow(0 .0625em 1.4375em rgba(209, 18, 22, 0.25));
    width: 1.4375em;
    margin-right: .75em;
  }
  &__title{
    font-size: 1em;
    line-height: 137%;
    font-weight: 600;
  }
  &__items{
    margin-top: 1.125em;
    @include grid100gap(.6875em);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    padding-left: .375em;
    &-icon{
      width: .875em;
      height: 1em;
      display: flex;
      align-items: center;
      margin-right: .5em;
      flex-shrink: 0;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-text{
      font-size: .8125em;
      line-height: 154%;
      a{
        font-weight: 600;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .selection4{
    padding: 0 0 2.375em;
  }

  .equipment{
    padding: 4.375em 0 6em;
    &__content{
      max-width: calc(50% - .625em);
    }
    &__img{
      width: calc(50% - .625em);
      padding: 0 .75em 1.125em 0;
    }
    .h2{
      line-height: 180%;
    }
    &__subtitle{
      margin-top: 1.875em;
    }
    .service__list{
      margin-top: 1.25em;
    }
  }

  .service-center{
    padding: 3.5em 0;
    &__top{
      gap: .625em;
    }
    &__grid{
      margin-top: 2.1875em;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: 1em;
    }
  }

  .center-card{
    background-image: url(../img/center-card-stroke2.svg);
    padding-bottom: 50.88%;
  }
}


@media screen and (max-width: 576px){
  .selection4{
    .selection__inner{
      background-image: url(../img/selection-bg7.webp);
    }
  }

  .equipment{
    padding: 4.375em 0 6em;
    background-image: url(../img/equipment-decor3.svg), url(../img/equipment-decor4.svg);
    &__content{
      max-width: 100%;
    }
    &__img{
      width: 100%;
      position: static;
      margin-top: 1.875em;
    }
  }

  .service-center{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
