.contacts{
  padding: 1em 0 3em;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 33.75em calc(100% - 34.5em);
    gap: .75em;
    margin-top: .25em;
  }
  &__col{
    padding-top: 2.625em;
  }
  &__map{
    height: 23.875em;
    width: auto;
    display: block;
  }
  &__items{
    margin-top: 1.875em;
    @include grid100gap(1.375em);
  }
}

.contact-item{
  display: flex;
  &__icon{
    width: 2.0625em;
    flex-shrink: 0;
    margin-right: 1.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    font-size: 1.0625em;
    line-height: 147%;
  }
}


@media screen and (max-width: 992px){
  .contacts{
    padding: .8125em 0 0;
    &__grid{
      grid-template-columns: 100%;
      gap: .625em;
      margin-top: .25em;
    }
    &__col{
      padding: 0;
    }
    &__map{
      width: 100%;
      height: auto;
    }
    &__items{
      margin-top: .9375em;
    }
    @media screen and (min-width: 577px){
      .catalog-item__nav{
        display: flex;
        .m-btn{
          margin: 0 .9375em 0 0;
        }
      }
    }
  }
}


