._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.header{
  position: relative;
  z-index: 5;
}

.mob-menu{
  padding-top: 4.75em;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    right: -1.625em;
    height: 4.75em;
    width: 500em;
    background-color: #484646;
    clip-path: polygon(100% 0, 100% calc(100% - .8125em), calc(100% - .8125em) 100%, 0 100%, 0 0);
  }
  &__top{
    padding: 1.25em 0;
    border-bottom: 1px solid #d6d6d6;
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 4.6875em;
    flex-shrink: 0;
    margin-right: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links{
    position: relative;
    width: calc(100% - 4.6875em - 2.5em);
    //display: flex;
    //align-items: center;
    //justify-content: space-between;
    .swiper{
      &-container{
        margin-left: calc(-.875em - 1px);
      }
      &-slide{
        width: auto;
        padding-right: .875em;
        white-space: nowrap;
      }
      &-button{
        margin: 0;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: .5em;
        height: auto;
        @include center;
        &-prev{
          left: -1em;
        }
        &-next{
          right: -1em;
        }
        svg{
          width: 100%;
        }
      }
    }
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: #727272;
    position: relative;
    &:hover{
      color: #d8d8d8;
      path{
        fill: #d8d8d8;
      }
    }
    &:active{
      color: $primary;
      path{
        fill: $primary;
      }
    }
    &-icon{
      width: 1.625em;
      height: 1.3125em;
      display: flex;
      align-items: center;
      margin-right: .5em;
      svg{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-title{
      font-size: .9375em;
      line-height: 120%;
    }
    @media screen and (min-width: 993px){
      &:before{
        content: '';
        right: 100%;
        margin-right: 1em;
        width: 1px;
        height: .875em;
        background-color: #c7c7c7;
        pointer-events: none;
      }
    }
  }
  &__btn{
    position: absolute;
    top: .875em;
    right: 0;
    width: 11.5625em;
    height: 2.625em;
    &._view2{
      display: none;
    }
  }
  &__bottom{
    position: absolute;
    top: 0;
    left: 0;
    height: 4.75em;
    display: flex;
    align-items: center;
    color: $white;
    width: calc(100% - 14.125em);
  }
  &__logo2{
    width: 8.3125em;
    margin-right: 2.125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__links2{
    .menu{
      display: flex;
      align-items: center;
      font-family: $font2;
      li{
        margin-right: 1.625em;
        position: relative;
      }
      a{
        font-size: .75em;
        text-transform: uppercase;
        &:hover{
          color: #aaa;
        }
        &:active{
          color: $primary;
        }
      }
    }
    .menu-item-has-children{
      &:before{
        content: '';
        position: absolute;
        top: 99%;
        width: 100%;
        height: 1em;
      }
      &:hover{
        .sub-menu{
          opacity: 1;
          pointer-events: unset;
        }
      }
    }
    .sub-menu{
      clip-path: polygon(100% 0,100% calc(100% - .8125em),calc(100% - .8125em) 100%,0 100%,0 0);
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      position: absolute;
      top: 100%;
      left: -1.5em;
      display: grid;
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .75em;
      background-color: #484646;
      padding: 3em 1.5em 1.25em;
      font-family: $font1;
      width: 66.75em;
      a{
        text-transform: unset;
        font-size: .9375em;
      }
    }
  }
  &__phone{
    margin-left: auto;
  }
  &__bg{
    display: none;
  }
}

.phone-link{
  display: flex;
  align-items: center;
  font-weight: 700;
  font-family: $font2;
  span{
    font-size: .6875em;
    padding: .2em 0;
    border-bottom: 1px solid transparent;
    transition: inherit;
  }
  svg{
    width: .875em;
    margin-right: .375em;
  }
  &:hover{
    color: #aaa;
    path{
      fill: #aaa;
    }
  }
  &:active{
    color: $white;
    path{
      fill: $white;
    }
    span{
      border-color: $white;
    }
  }
}

.header-mob{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    position: relative;
    z-index: 5;
    .container{
      padding: 0;
    }
    &._open{
      .mob-menu{
        transform: translateX(0);
        &__bg{
          display: block;
        }
      }
    }
  }

  .mob-menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 18.3125em;
    background-color: $white;
    z-index: 10;
    padding: .875em 1.5em 2em;
    overflow-y: auto;
    clip-path: polygon(100% 0, 100% calc(100% - 1.5em), calc(100% - 1.5em) 100%, 0 100%, 0 0);
    transition: all .5s;
    transform: translateX(-180%);
    max-height: calc(100 * var(--vh) - 1.75em);
    height: auto;
    &:before{
      display: none;
    }
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background-color: rgba(#535353,.6);
    }
    &__top{
      display: block;
      padding: 0;
      border: none;
    }
    &__logo{
      width: 4em;
      display: block;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
      border-top: $border;
      border-bottom: $border;
      margin-top: .75em;
      padding: .9375em 0;
      width: 100%;
      .swiper{
        &-container{
          margin: 0;
        }
        &-wrapper{
          flex-direction: column;
          align-items: flex-start;
          transform: unset !important;
        }
        &-slide{
          padding: 0;
          white-space: unset;
          &:last-child{
            .mob-menu__link{
              margin-bottom: 0;
            }
          }
        }
        &-button{
          display: none;
        }
      }
    }
    &__link{
      display: flex;
      margin-bottom: 1.5em;
      align-items: flex-start;
      &-icon{
        width: 1.625em;
        margin-right: .625em;
        flex-shrink: 0;
        svg{
          width: 100%;
          max-width: unset;
          max-height: unset;
        }
      }
    }
    &__bottom{
      position: static;
      width: auto;
      color: $black;
      display: block;
      height: auto;
      margin-top: 1.25em;
    }
    &__logo2{
      display: none;
    }
    &__links2{
      padding-bottom: 1.25em;
      border-bottom: $border;
      .menu{
        @include grid100gap(1.25em);
        a{
          font-size: .9375em;
        }
      }
      .sub-menu{
        display: none;
      }
    }
    &__phone{
      margin: 1.25em auto 0 0;
      display: inline-flex;
      color: #727272 !important;
      path{
        fill: #727272 !important;
      }
    }
    &__btn{
      flex-shrink: 0;
      top: unset;
      position: relative;
      margin-top: 1.5em;
      width: 100%;
      clip-path: polygon( 4.845% 2.716%,4.845% 2.716%,4.955% 2.213%,5.07% 1.759%,5.19% 1.355%,5.314% 1.001%,5.441% 0.7%,5.572% 0.45%,5.705% 0.255%,5.84% 0.114%,5.978% 0.029%,6.116% 0%,98% 0%,98% 0%,98.324% 0.156%,98.632% 0.607%,98.919% 1.329%,99.181% 2.297%,99.414% 3.487%,99.614% 4.874%,99.777% 6.434%,99.898% 8.142%,99.974% 9.974%,100% 11.905%,100% 62.926%,100% 62.926%,99.994% 63.869%,99.975% 64.8%,99.944% 65.715%,99.902% 66.61%,99.848% 67.482%,99.782% 68.326%,99.706% 69.138%,99.619% 69.914%,99.522% 70.651%,99.414% 71.344%,95.186% 96.513%,95.186% 96.513%,95.069% 97.153%,94.946% 97.733%,94.815% 98.251%,94.679% 98.705%,94.537% 99.094%,94.391% 99.416%,94.24% 99.669%,94.086% 99.852%,93.93% 99.963%,93.772% 100%,2% 100%,2% 100%,1.676% 99.844%,1.368% 99.393%,1.081% 98.671%,0.819% 97.703%,0.586% 96.513%,0.386% 95.126%,0.223% 93.566%,0.102% 91.858%,0.026% 90.026%,0% 88.095%,0% 32.088%,0% 32.088%,0.008% 31.027%,0.031% 29.984%,0.07% 28.962%,0.124% 27.968%,0.191% 27.008%,0.273% 26.086%,0.368% 25.209%,0.476% 24.382%,0.596% 23.61%,0.728% 22.899%,4.845% 2.716% );
      &._view1{
        display: none;
      }
      &._view2{
        display: flex;
      }
    }
  }

  .header-mob{
    display: block;
    color: $white;
    padding: .375em 1.5625em .75em;
    background-color: $black;
    clip-path: polygon(100% 0, 100% calc(100% - .875em), calc(100% - .875em) 100%, 0 100%, 0 0);
    &__logos{
      width: 12.54em;
      margin: 0 auto .375em;
      img{
        width: 100%;
        display: block;
      }
    }
    &__bottom{
      border-top: .5px solid $white;
      padding-top: .625em;
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
  }

  .burger{
    width: 1.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}


