.selection{
  color: $white;
  &__inner{
    background-image: url(../img/selection-bg.webp);
    background-size: cover;
    background-position: center top;
    border-radius: .5625em;
    padding: 3.5em 1em 2.125em;
    text-align: center;
  }
  &__desc{
    width: 37.1875em;
    max-width: 100%;
    margin: .5em auto 0;
  }
  &__btn{
    //box-shadow: 0 .5625em 1.25em rgba(209, 18, 22, 0.16);
    &:not(:hover){
      background-image: url(../img/btn-bg.svg);
      color: $white;
    }
    svg{
      width: .875em;
    }
  }
}

._mt30{
  margin-top: 1.875em;
}

.selection-index{
  margin-top: -15.875em;
  margin-bottom: 2.625em;
  position: relative;
  z-index: 2;
}

.advantages{
  padding: 2.625em 0;
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.advantage-card{
  padding-bottom: 40.82%;
  background-image: url(../img/advantage-stroke.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: 0 0;
  position: relative;
  &__inner{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding: 1.75em 1.5em 1.25em;
  }
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
  }
  &__icon{
    flex-shrink: 0;
    width: 2.5em;
    filter: drop-shadow(0 .0625em 1.4375em rgba(209, 18, 22, 0.25));
    margin-right: 1.0625em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 122%;
    font-weight: 600;
  }
  &__num{
    position: absolute;
    right: .875em;
    top: .625em;
    font-size: 1em;
    font-family: $font2;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: rgba(0, 0, 0, 0.6);
  }
}


@media screen and (max-width: 992px){
  .selection{
    .container{
      padding: 0;
    }
    &__inner{
      padding: 6.625em 1.25em;
    }
    .h2{
      padding: 1em 1.5em;
    }
    &__desc{
      margin-top: 0;
    }
    &__nav{
      margin-top: 2.1875em;
    }
  }

  .selection-index{
    margin: -2.25em 0 2.25em;
  }

  .advantages{
    padding: 2.25em 0;
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .5625em));
      gap: 1.125em;
    }
  }

  .advantage-card{
    font-size: .875em;
    &__title{
      font-size: 1.0625em;
    }
  }
}


@media screen and (max-width: 576px){
  .selection{
    &__inner{
      background-image: url(../img/selection-bg2.webp);
    }
  }

  .advantages{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .advantage-card{
    background-image: url(../img/advantage-stroke2.svg);
    font-size: 1em;
    padding-bottom: 46.47%;
  }
}
