.about-main2{
  &__subtitle{
    margin-top: .6875em;
  }
  .about-main__content{
    max-width: 35em;
  }
  &__list{
    margin-top: 1.5em;
    @include grid100gap(.75em);
    &-item{
      display: flex;
      align-items: flex-start;
      padding-right: 1.25em;
      img{
        filter: drop-shadow(0 1px 1.5em rgba(209, 18, 22, 0.25));
        width: 1.9375em;
        margin-right: .9375em;
        flex-shrink: 0;
      }
      &:not(:last-child){
        padding-bottom: 1.25em;
        background-image: url(../img/list-line.svg);
        background-size: 100% auto;
        background-repeat: no-repeat;
        background-position: right bottom;
      }
    }
    &-text{
      padding-top: .1875em;
      font-size: 1em;
      line-height: 156%;
      span{
        font-weight: 700;
        color: $primary;
      }
    }
  }
  .about-main__img{
    width: calc(100% - 31em);
    right: -3.125em;
    img{
      width: 100%;
      object-fit: cover;
    }
    @media screen and (min-width: 1301px){
      left: unset;
      right: calc(((100vw - 1300px) / 2 - -3.875em) * -1);
      width: calc(100vw - 40.875em - (100vw - 1300px) / 2 - 3.125em);
    }
  }
}

.head-office{
  padding: 2.625em 0 5.625em;
  background-color: $gray;
  background-image: url(../img/head-office-decor.svg),url(../img/head-office-decor2.svg);
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: left top -2px, left bottom -2px;
  @media screen and (min-width: 1401px){
    padding-top: 5em;
    padding-bottom: 8em;
  }
  &__inner{
    display: grid;
    grid-template-columns:calc(100% - 32.125em) 27.875em;
    gap: 1.25em 4.25em;
    align-items: center;
  }
  &__img{
    width: 100%;
    display: block;
  }
}

.solutions{
  padding: 1.5em 0 2.5em;
  &__inner{
    display: grid;
    grid-template-columns: 32.75em calc(100% - 35.625em);
    gap: 0 2.875em;
    position: relative;
  }
  &__col{
    padding-top: 3.125em;
  }
  &__img{
    width: 32.75em;
    max-width: 100%;
    display: block;
    position: absolute;
    top: 3.125em;
    left: 0;
  }
  &__subtitle{
    font-size: 1em;
    line-height: 156%;
    font-weight: 600;
    margin-top: 1em;
  }
  &__info{
    display: flex;
    align-items: center;
    margin-top: 1.625em;
    font-weight: 700;
    color: $primary;
    img{
      width: 3em;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
      line-height: 156%;
    }
  }
  &__list{
    margin-top: 1.75em;
    gap: .9375em;
    li{
      line-height: 131%;
    }
  }
}

.selection6{
  .selection__inner{
    padding-top:  4.375em;
    padding-bottom: 3.125em;
    background-image: url(../img/selection-bg8.svg);
  }
  .h2{
    font-size: 1.375em;
    font-weight: 700;
    span{
      font-size: .81em;
    }
  }
}

.equipment2{
  padding: 3.5em 0;
  &__decor{
    width: 100%;
    display: block;
    margin-bottom: 2.875em;
    &._view2{
      display: none;
    }
  }
  &__inner{
    display: grid;
    grid-template-columns: 29.625em calc(100% - 29.625em - 5.5em);
    gap: 5.5em;
    position: relative;
  }
  &__col2{
    max-width: 37.5em;
  }
  &__subtitle{
    margin-top: 1em;
    span{
      color: $primary;
    }
  }
  &__desc{
    margin-top: 1.5em;
  }
  &__list{
    margin-top: 1.25em;
  }
  .equipment-slider{
    max-width: 100%;
    width: 29.625em;
    position: absolute;
    left: 0;
    top: 0;
  }
}

.equipment-slider{
  overflow: hidden;
  &__img{
    clip-path: polygon( 0.333% 90.159%,0.333% 90.159%,0.272% 90.078%,0.217% 89.992%,0.167% 89.901%,0.124% 89.805%,0.087% 89.705%,0.056% 89.602%,0.032% 89.495%,0.014% 89.386%,0.003% 89.275%,-0% 89.162%,-0% 1.366%,-0% 1.366%,0.014% 1.145%,0.054% 0.934%,0.118% 0.738%,0.203% 0.559%,0.309% 0.4%,0.432% 0.264%,0.57% 0.152%,0.721% 0.07%,0.884% 0.018%,1.055% 0%,92.139% 0%,92.139% 0%,92.214% 0.003%,92.288% 0.014%,92.361% 0.031%,92.432% 0.054%,92.503% 0.084%,92.571% 0.12%,92.638% 0.162%,92.702% 0.211%,92.763% 0.265%,92.822% 0.325%,99.628% 7.817%,99.628% 7.817%,99.696% 7.899%,99.757% 7.987%,99.812% 8.081%,99.861% 8.18%,99.902% 8.285%,99.937% 8.393%,99.964% 8.506%,99.984% 8.621%,99.996% 8.739%,100% 8.858%,100% 98.634%,100% 98.634%,99.986% 98.855%,99.946% 99.066%,99.882% 99.262%,99.796% 99.441%,99.691% 99.6%,99.568% 99.736%,99.43% 99.847%,99.278% 99.93%,99.116% 99.982%,98.945% 100%,8.856% 100%,8.856% 100%,8.776% 99.996%,8.697% 99.984%,8.619% 99.965%,8.542% 99.938%,8.468% 99.904%,8.396% 99.863%,8.326% 99.815%,8.259% 99.76%,8.195% 99.699%,8.134% 99.631%,0.333% 90.159% );
    @include imgCover(77.2%);
  }
  .swiper-nav{
    margin-top: 2em;
    justify-content: unset;
  }
}


@media screen and (max-width: 992px){
  .about-main2{
    .about-main__img{
      position: static;
      width: calc(100% + 2em);
      margin: 1.5em -2em 1.25em 0;
    }
    &__list{
      &-text{
        padding-top: 0;
      }
      img{
        margin-top: .5em;
      }
    }
  }

  .head-office{
    padding: 6.25em 0 2.5em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      width: calc(100% + 3.125em);
      margin-left: -1.5625em;
    }
    .h2{
      max-width: 20em;
    }
  }

  .solutions{
    padding: 3.25em 0 2.375em;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      display: none;
    }
    &__img{
      position: static;
      margin: 2.625em 0 1.5em;
    }
    &__subtitle{
      margin-top: .75em;
    }
    &__info{
      align-items: flex-start;
      img{
        margin-right: .625em;
      }
    }
    &__list{
      margin-top: 1.5em;
    }
  }

  .selection6{
    .selection__inner{
      padding-top: 5.375em;
      padding-bottom: 10.125em;
      background-image: url(../img/selection-bg9.svg);
    }
    .h2{
      //text-transform: unset;
      line-height: 168%;
      span{
        display: block;
        font-size: .73em;
        padding: 1em 0;
        line-height: 156%;
        font-weight: 500;
        font-family: $font1;
        letter-spacing: 0.02em;
        text-transform: none;
      }
    }
  }

  .equipment2{
    .h2{
      font-size: 1.5625em;
    }
    &__decor{
      margin-bottom: 1em;
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__inner{
      display: block;
    }
    .equipment-slider{
      position: static;
      margin: 2.5em 0 1.625em;
    }
  }

  .equipment-slider{
    .swiper-nav{
      justify-content: space-between;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main2{
    .about-main__nav .m-btn{
      width: 100%;
    }
  }

  .head-office{
    background-image: url(../img/head-office-decor3.svg),url(../img/head-office-decor4.svg);
  }
}
