.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(83, 83, 83, 0.6);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background-color: #fff;
        background-image: url(../img/modal-bg.webp);
        background-size: cover;
        background-position: right bottom;
        width: 49.625em;
        max-width: calc(100% - 3.125em);
        position: relative;
        z-index: 3;
        padding: 2em 4em 2.5em;
        border-radius: .3125em;
        display: inline-block;
        clip-path: polygon( 0% 9.651%,0% 9.651%,0.002% 9.533%,0.009% 9.416%,0.02% 9.302%,0.036% 9.19%,0.055% 9.082%,0.079% 8.978%,0.106% 8.879%,0.137% 8.785%,0.172% 8.696%,0.211% 8.614%,4.418% 0.352%,4.418% 0.352%,4.454% 0.287%,4.491% 0.228%,4.53% 0.176%,4.571% 0.13%,4.613% 0.091%,4.656% 0.059%,4.7% 0.033%,4.745% 0.015%,4.791% 0.004%,4.837% 0%,99.37% 0%,99.37% 0%,99.472% 0.018%,99.569% 0.071%,99.66% 0.155%,99.742% 0.268%,99.816% 0.407%,99.878% 0.569%,99.93% 0.751%,99.968% 0.95%,99.992% 1.164%,100% 1.389%,100% 91.104%,100% 91.104%,99.997% 91.236%,99.989% 91.365%,99.975% 91.491%,99.956% 91.613%,99.932% 91.73%,99.904% 91.842%,99.87% 91.948%,99.833% 92.047%,99.79% 92.139%,99.744% 92.222%,95.129% 99.729%,95.129% 99.729%,95.096% 99.779%,95.061% 99.825%,95.026% 99.865%,94.989% 99.9%,94.952% 99.93%,94.914% 99.955%,94.875% 99.975%,94.835% 99.989%,94.795% 99.997%,94.755% 100%,0.63% 100%,0.63% 100%,0.528% 99.982%,0.431% 99.929%,0.34% 99.845%,0.258% 99.732%,0.184% 99.593%,0.122% 99.431%,0.07% 99.249%,0.032% 99.05%,0.008% 98.836%,0% 98.611%,0% 9.651% );
    }
    &__top{
        text-align: center;
    }
    .close-btn{
        position: absolute;
        top: .875em;
        right: .875em;
    }
}

.close-btn{
    width: 1.75em;
    @include center;
    flex-shrink: 0;
    svg{
        width: 100%;
    }
    &:hover{
        line{
            stroke: #d8d8d8;
        }
    }
    &:active{
        line{
            stroke: $primary;
        }
    }
}

.modal-form{
    margin-top: 1.625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    &__nav{
        margin-top: .5em;
    }
}

._full{
    grid-column: 1/-1;
}

.cookie-modal {
    position: fixed;
    bottom: 2.75em;
    right: 4.875em;
    background: $white;
    z-index: 31;
    padding: 1.5em 1.875em;
    width: 28.75em;
    max-width: calc(100% - 2em);
    transition: all .3s;
    opacity: 0;
    border-radius: .25em;
    pointer-events: none;
    box-shadow: 0 -.25em 1.25em 0 rgba(0, 0, 0, 0.16);
    clip-path: polygon(1.5em 0, 100% 0, 100% 100%, 0 100%, 0 1.5em);

    &._active {
        opacity: 1;
        pointer-events: unset;
    }
    &__close{
        position: absolute;
        top: .4375em;
        right: .4375em;
        width: 1.3125em;
        @include center;
        svg{
            width: 100%;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
    &__desc{
        font-size: .625em;
        line-height: 170%;
        a{
            color: $primary;
        }
    }
    &__nav {
        display: flex;
        margin-top: .625em;
    }
    &__btn {
        width: 7em;
        height: 1.5em;
        span{
            font-size: .5em;
        }
    }
}


