.about,
.service{
  &__top{
    @include grid100gap(1.125em);
  }
  &__nav{
    display: flex;
    padding-top: 1.875em;
    margin-top: auto;
  }
}

.about{
  padding: 2.1875em 0 .625em;
  &__inner{
    position: relative;
  }
  &__img{
    position: absolute;
    top: 0;
    right: 36.75em;
    height: 100%;
    img{
      margin-left: auto;
      height: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
    @media screen and (min-width: 1441px){
      width: calc(100vw - 36.75em - (100vw - 1300px) / 2 - 3.125em);
      clip-path: polygon( 40.541% 0%,0% 0%,0% 100%,75.196% 100%,99.316% 100%,99.316% 100%,99.427% 99.98%,99.532% 99.921%,99.63% 99.827%,99.72% 99.701%,99.8% 99.547%,99.868% 99.366%,99.924% 99.163%,99.965% 98.941%,99.991% 98.703%,100% 98.452%,100% 91.634%,100% 27.21%,100% 27.21%,99.997% 27.056%,99.987% 26.906%,99.97% 26.759%,99.948% 26.617%,99.919% 26.481%,99.885% 26.352%,99.846% 26.231%,99.801% 26.118%,99.751% 26.016%,99.696% 25.924%,91.168% 13.003%,91.168% 13.003%,91.134% 12.954%,91.098% 12.91%,91.062% 12.871%,91.025% 12.837%,90.987% 12.808%,90.948% 12.784%,90.908% 12.766%,90.869% 12.752%,90.828% 12.744%,90.788% 12.741%,51.045% 12.741%,51.045% 12.741%,51.01% 12.739%,50.976% 12.733%,50.941% 12.723%,50.907% 12.71%,50.874% 12.692%,50.84% 12.67%,50.808% 12.645%,50.776% 12.616%,50.744% 12.583%,50.713% 12.547%,40.872% 0.194%,40.872% 0.194%,40.842% 0.158%,40.81% 0.125%,40.778% 0.096%,40.745% 0.071%,40.712% 0.049%,40.678% 0.032%,40.644% 0.018%,40.61% 0.008%,40.575% 0.002%,40.541% 0% );
      height: 25em;
      img{
        width: 100%;
        object-fit: cover;
      }
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__content{
    padding-top: .375em;
    margin-left: auto;
    width: 32em;
    min-height: 25em;
    display: flex;
    flex-direction: column;
  }
  &__subtitle{
    margin-top: 1em;
  }
  &__list{
    margin-top: .5em;
  }
}

.text ul,
.m-list{
  @include grid100gap(.4375em);
  li{
    font-size: 1em;
    line-height: 156%;
    padding-left: 1.75em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      background-image: url(../img/li-bullet.svg);
      width: 1.1875em;
      height: 1.1875em;
      @include bgImgSize(100%);
      top: .25em;
      left: 0;
    }
  }
}

.service{
  margin: .625em 0 0;
  padding: 8.5em 0 20.25em;
  background-color: $gray;
  background-image: url(../img/service-decor.svg),url(../img/service-decor2.svg);
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: left top -.25em, left bottom -.25em;
  &__inner{
    position: relative;
  }
  &__content{
    min-height: 20.625em;
    width: 31.5em;
    max-width: 100%;
  }
  &__img{
    position: absolute;
    width: calc(100% - 34em - 3.875em);
    top: 0;
    right: 2.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__list{
    margin-top: 1.25em;
    @include grid100gap(.75em);
    max-width: 29.5em;
    width: 100%;
    li{
      min-height: 1.9375em;
      display: flex;
      align-items: center;
      padding: 0 .375em;
      background-image: url(../img/service-stroke.svg);
      @include bgImgSize(100%);
    }
    img{
      filter: drop-shadow(0 .0625em .25em rgba(209, 18, 22, 0.2));
      width: 1.1875em;
      margin-right: .5em;

    }
    span{
      font-size: 1em;
      line-height: 156%;
    }
  }
}


@media screen and (max-width: 992px){
  .about,
  .service{
    &__img{
      position: static;
      height: auto;
      width: calc(100% + 1.5625em);
      margin: 1.875em 0 0 -1.5625em;
      img{
        width: 100%;
        height: auto;
      }
    }
    &__content{
      width: 100%;
      padding: 0;
      min-height: unset;
    }
    &__nav{
      padding-top: 2.1875em;
    }
  }

  .about{
    padding: 2.25em 0 1.625em;
    &__top{
      gap: .625em;
    }
    &__desc{
      font-size: 1.0625em;
      line-height: 147%;
    }
    &__subtitle{
      margin-top: 1.875em;
    }
    &__list{
      margin-top: 1.125em;
    }
  }

  .service{
    padding: 4.875em 0 7em;
    background-image: url(../img/service-decor.svg);
    &__top{
      gap: .375em;
    }
    &__list{
      margin-top: 1.875em;
      gap: .9375em;
      li{
        padding: 0;
        background-image: unset;
        min-height: unset;
        align-items: flex-start;
        img{
          margin-top: .25em;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .service{
    background-image: url(../img/service-decor3.svg);
    background-position: left top;
  }
}
