.catalog-table{
  margin-top: 2.1875em;
  &:first-child{
    margin-top: 0;
  }
  table{
    width: 100%;
    border-spacing: 0;
  }
  th,td{
    text-align: center;
    &:first-child{
      width: 16.875em;
      text-align: left;
      padding-left: 1.75em;
    }
  }
  th{
    font-weight: 700;
    font-family: $font2;
    height: 3.625em;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 19px;
      background-repeat: no-repeat;
      background-image: url(../img/col-border.svg);
    }
    &:before{
      content: '';
      position: absolute;
      height: 1px;
      width: 100%;
      right: 18px;
      top: 0;
      background-color: #e1e1e1;
    }
    &:first-child{
      font-weight: 400;
      border-left: 1px solid #e1e1e1;
      &:before{
        width: calc(100% - 18px);
      }
    }
  }
  td{
    background-color: $gray;
    height: 2.75em;
    border-top: 1px solid #999999;
    border-right: 1px solid #e1e1e1;
    &:last-child{
      border-right: none;
    }
  }
  tr:first-child{
    td{
      border-top: none;
    }
  }
}

.catalog-table2{
  margin-top: 2.1875em;
  background-image: url(../img/table-border.svg);
  background-size: 100% auto;
  height: 3.6875em;
  display: grid;
  grid-template-columns: 16.875em calc(100% - 16.875em);
  &__col1{
    position: relative;
    padding-left: 1.75em;
    padding-right: 1em;
    display: flex;
    align-items: center;
    font-family: $font2;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 19px;
      background-repeat: no-repeat;
      background-image: url(../img/col-border.svg);
    }
  }
  &__col2{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-right: 1.6875em;
  }
  &__sub-col{
    width: 14.25em;
    text-align: center;
    font-family: $font2;
    font-weight: 700;
  }
  .product-card2__nav{
    padding: 0;
    width: 13.5em;
    margin: 0;
  }
}

