  padding: 8em 0 .875em;
  background-color: $gray;
  &__inner{
    min-height: 22.5625em;
    position: relative;
  }
  &__content{
    max-width: 28.75em;
    padding-top: 3em;
  }
  &__img{
    height: 24.5625em;
    width: 43.375em;
    position: absolute;
    right: 0;
    bottom: 0;
    img{
      max-width: 100%;
      max-height: 100%;
      object-fit: cover;
    }
  }
  &__text{
    margin-top: 1.125em;
  }
  .catalog-item__nav{
    margin-top: 1.625em;
  }
}


@media screen and (max-width: 992px){
  .catalog-table{
    width: calc(100% + 3.125em);
    margin-left: -1.5625em;
    padding: 0 1.5625em .5em;
    overflow-x: auto;
    table{
      font-size: .75em;
      min-width: 75em;
    }
  }

  .catalog-table2{
    background-image: unset;
    display: block;
    position: relative;
    height: unset;
    border-bottom: 1px solid #e1e1e1;
    border-left: 1px solid #e1e1e1;
    &:before{
      content: '';
      position: absolute;
      height: 1px;
      width: calc(100% - 18px);
      right: 18px;
      top: 0;
      background-color: #e1e1e1;
    }
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      height: 100%;
      width: 19px;
      background-repeat: no-repeat;
      background-image: url(../img/col-border.svg);
    }
    &__col1{
      display: none;
    }
    &__col2{
      min-height: 3.25em;
      padding: 0 1em;
    }
    &__sub-col{
      width: auto;
    }
    .product-card2__nav{
      width: auto;
    }
    .product-card2__nav-wrap{
      padding: 0;
    }
    .product-card2__btn{
      position: static;
      width: auto;
      height: auto;
      background: unset !important;
    }
  }

  .catalog-main{
    padding: 5.875em 0 3em;
    &__inner{
      margin-top: .5625em;
    }
    &__content{
      max-width: 48%;
      padding: 0;
    }
    &__img{
      left: 45%;
      width: 60%;
      height: auto;
      top: 0;
      bottom: unset;
    }
  }
}


