  }
  .map{
    height: 28.625em;
  }
}

.dealers-filter{
  margin-top: 2em;
  .region-select{
    width: 26.72em;
    margin: 0;
    select{
      text-transform: uppercase;
      color: $black;
      font-size: .8125em;
      height: 3em;
      background-image: url(../img/line5.svg),url(../img/arrow2.svg);
      background-size: auto 1.23em,1.46em auto;
      background-position: right bottom,right 1em center;
    }
  }
  .m-check{
    margin-right: 2em;
    &:last-child{
      margin-right: 0;
    }
    label{
      color: #454242;
      display: flex;
      align-items: center;
      &:before,
      &:after{
        font-size: 1.1em;
      }
    }
    span{
      font-size: .8125em;
    }
  }
}

.map-view{
  margin-top: 2.5em;
  position: relative;
}

.dealer-card{
  position: absolute;
  left: 3.125em;
  top: 50%;
  transform: translateY(-50%);
  width: 16.5625em;
  background-color: $white;
  padding: 1.562em 1em 1em;
  box-shadow: 0px 4px 20px rgba(109, 109, 109, 0.25);
  border-radius: .75em;
  transition: opacity .3s;
  opacity: 0;
  pointer-events: none;
  @media screen and (min-width: 1301px) {
    left: calc((100vw - 1300px) / 2 + 2.25em);
  }
  &._show{
    opacity: 1;
    pointer-events: unset;
  }
  &:before{
    content: '';
    position: absolute;
    top: .875em;
    left: 0;
    width: .1875em;
    height: 3.625em;
    background-image: url(../img/line6.svg);
    @include bgImgSize(100%);
  }
  &__close{
    position: absolute;
    top: 1em;
    right: 1em;
    width: 1.25em;
    @include center;
    img{
      width: 100%;
    }
  }
  &__title{
    font-size: .75em;
    line-height: 167%;
    text-transform: uppercase;
    font-family: $font2;
    letter-spacing: .02em;
    font-weight: 700;
  }
  &__time{
    color: #898989;
    font-size: 0.7em;
    line-height: 222%;
    margin-top: .75em;
  }
  &__props{
    margin-top: .31em;
    @include grid100gap(.375em);
    padding-bottom: .94em;
    border-bottom: 1px solid #D9D9D9;
  }
  &__prop{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: .8125em;
      margin-right: .44em;
      flex-shrink: 0;
    }
    span{
      font-size: .625em;
      line-height: 140%;
    }
    &:not(._active){
      display: none;
    }
  }
  &__items{
    margin-top: .75em;
    @include grid100gap(.375em);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    color: #454242;
    &-icon{
      width: .625em;
      height: .6875em;
      @include center;
      margin-top: .05em;
      margin-right: .25em;
      flex-shrink: 0;
      font-size: 1.4em;
      img{
        max-width: 100%;
        max-height: 100%;
      }
    }
    &-text{
      font-size: .7em;
      line-height: 175%;
    }
  }
  &__btn{
    width: 100%;
    height: 2em;
    margin-top: 2em;
    &:before{
      clip-path: polygon( 3.355% 4.126%,3.355% 4.126%,3.479% 3.365%,3.609% 2.678%,3.746% 2.065%,3.887% 1.527%,4.034% 1.068%,4.186% 0.688%,4.34% 0.39%,4.498% 0.174%,4.659% 0.044%,4.821% 0%,97.835% 0%,97.835% 0%,98.187% 0.205%,98.52% 0.797%,98.83% 1.744%,99.114% 3.015%,99.366% 4.576%,99.582% 6.397%,99.758% 8.444%,99.89% 10.686%,99.972% 13.091%,100% 15.625%,100% 62.164%,100% 62.164%,99.992% 63.54%,99.967% 64.895%,99.926% 66.222%,99.869% 67.514%,99.798% 68.763%,99.711% 69.962%,99.611% 71.106%,99.496% 72.186%,99.369% 73.195%,99.228% 74.127%,95.605% 96.102%,95.605% 96.102%,95.485% 96.779%,95.359% 97.391%,95.229% 97.936%,95.093% 98.413%,94.954% 98.82%,94.811% 99.156%,94.664% 99.42%,94.516% 99.61%,94.365% 99.725%,94.213% 99.764%,2.165% 99.764%,2.165% 99.764%,1.813% 99.56%,1.48% 98.968%,1.17% 98.02%,0.886% 96.749%,0.634% 95.188%,0.418% 93.367%,0.242% 91.32%,0.11% 89.078%,0.028% 86.674%,0% 84.139%,0% 33.265%,0% 33.265%,0.008% 31.961%,0.03% 30.675%,0.067% 29.413%,0.118% 28.182%,0.182% 26.986%,0.261% 25.833%,0.352% 24.728%,0.455% 23.678%,0.571% 22.689%,0.699% 21.766%,3.355% 4.126% );
    }
    span{
      font-size: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .dealers-filter{
    display: block;
    margin-top: 1em;
    &__checks{
      margin-top: 2em;
    }
  }

  .dealer-card{
    left: 1.5625em;
  }
}


@media screen and (max-width: 576px){
  .dealers{
    &__top{
      display: block;
    }
    .tabs{
      margin-top: .25em;
      font-size: 1.25em;
    }
    .map{
      height: 22em;
    }
  }

  .dealers-filter{
    &__checks{
      margin-top: 1em;
      display: block;
      .m-check{
        margin: 0 0 1em;
      }
    }
    .region-select{
      width: 100%;
    }
  }

  .dealer-card{
    left: 50%;
    transform: translate(-50%,-50%);
  }
}
