.map{
  border-radius: .5625em;
  overflow: hidden;
  height: 19.625em;
}

.first-block{
  margin-top: -7.5em;
}

.service-main{
  background-color: $gray;
  padding: 8em 0 10.25em;
  background-image: url(../img/service-decor4.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left bottom -4px;
  &__content{
    max-width: 32.5em;
    margin-top: 2.625em;
  }
  &__text{
    margin-top: .8125em;
  }
  &__nav{
    display: flex;
    padding-top: 2.1875em;
  }
  &__inner{
    position: relative;
  }
  &__img{
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 44.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .first-block{
    margin-top: -5.25em;
  }

  .service-main{
    background-color: $gray;
    padding: 7.3125em 0 4.75em;
    &__content{
      min-height: unset;
      margin-top: 0;
      display: flex;
      flex-direction: column;
    }
    &__img{
      width: 100%;
      position: static;
      margin: 2.5em 0 1.625em;
      transform: unset;
    }
    &__nav{
      margin-top: auto;
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .service-main{
    background-image: url(../img/service-decor5.svg);
    &__nav{
      .m-btn{
        width: 100%;
      }
    }
  }
}
