.catalog-detail{
  padding: 2.5em 0 3em;
  margin: 5em 0 2em;
  background-color: $gray;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    width: 100%;
    height: 2.0625em;
    left: 0;
    background-size: auto 100%;
    background-repeat: no-repeat;
  }
  &:before{
    bottom: 100%;
    margin-bottom: -1px;
    background-image: url(../img/detail-bg.svg);
    background-position: right top;
  }
  &:after{
    top: 100%;
    margin-top: -1px;
    background-image: url(../img/detail-bg2.svg);
    background-position: left top;
  }
  @media screen and (min-width: 1441px){
    &:before{
      background-image: url(../img/detail-bg5.svg);
    }
    &:after{
      background-image: url(../img/detail-bg6.svg);
    }
  }
  &__link{
    width: 2.375em;
    @include center;
    flex-shrink: 0;
    svg{
      width: 100%;
    }
    &:hover{
      path{
        stroke: #d8d8d8;
      }
    }
    &:active{
      path{
        stroke: $primary;
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: 28.25em calc(100% - 31.75em);
    gap: 1.625em 3.5em;
    margin-top: 1.125em;
  }
  &__subtitle{
    font-size: 1em;
    line-height: 287%;
    letter-spacing: 0.02em;
    font-weight: 500;
  }
  &__desc{
    margin-top: .5em;
  }
  &__col{
    padding-right: 1.75em;
    border-right: 1px solid #b3b3b3;
  }
}

.prop-item2{
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 2.5em;
  border-bottom: 1px solid #e1e1e1;
  padding: .125em 2em .125em 1.625em;
  &__title{
    font-size: 1em;
    line-height: 131%;
    letter-spacing: 0.02em;
  }
  &__desc{
    font-weight: 700;
    font-size: .9375em;
    line-height: 140%;
    letter-spacing: 0.02em;
  }
}

.product-advantages{
  margin-top: 2.25em;
  padding-top: 3.5em;
  border-top: 1px solid #b3b3b3;
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: 1fr 1.1fr 1fr 1fr;
    gap: 2em;
  }
}

.advantage-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: 1em;
  }
  &__title{
    font-weight: 600;
    font-size: 1.125em;
    line-height: 122%;
  }
}


@media screen and (max-width: 992px){
  .catalog-detail{
    padding: 4.375em 0 6.125em;
    &:before{
      top: 0;
      bottom: unset;
      background-image: url(../img/detail-bg3.svg);
    }
    &:after{
      bottom: 0;
      top: unset;
      background-image: url(../img/detail-bg4.svg);
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    &__col{
      padding: 0 0 1.875em;
      border-right: none;
      border-bottom: 1px solid #b3b3b3;
    }
    &__desc{
      font-size: .9375em;
      line-height: 167%;
    }
  }

  .prop-item2{
    min-height: unset;
    padding: .5em 0;
    &__title,
    &__desc{
      font-size: .875em;
      line-height: 150%;
    }
  }

  .product-advantages{
    padding-top: 2.1875em;
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2.1875em 2em;
    }
  }
}


@media screen and (max-width: 576px){
  .product-advantages{
    &__items{
      grid-template-columns: 100%;
    }
  }
}
