.collection{
  padding: .3125em 0 2.1875em;
  &__nav{
    margin-top: 2.75em;
    .m-btn{
      margin-right: 1em;
    }
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
  }
}

.collection-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.6875em);
    }
    &-slide{
      width: 20%;
      padding-right: 1.6875em;
    }
  }
}

.product-card{
  padding: 0 0 .625em .625em;
  min-height: 18.125em;
  height: 100%;
  display: flex;
  flex-direction: column;
  background-image: url(../img/card-stroke.svg);
  background-size: calc(100% - .375em) auto;
  background-position: left bottom;
  background-repeat: no-repeat;
  &__inner{
    flex-grow: 1;
    height: 100%;
    background-color: $gray;
    border-radius: .3125em;
    clip-path: polygon( 0% 1.971%,0% 1.971%,0.031% 1.681%,0.123% 1.405%,0.268% 1.148%,0.464% 0.913%,0.704% 0.704%,0.984% 0.525%,1.299% 0.379%,1.644% 0.271%,2.014% 0.203%,2.404% 0.179%,97.596% 0.179%,97.596% 0.179%,97.986% 0.203%,98.356% 0.271%,98.701% 0.379%,99.016% 0.525%,99.296% 0.704%,99.536% 0.913%,99.732% 1.148%,99.877% 1.405%,99.969% 1.681%,100% 1.971%,100% 91.587%,100% 91.587%,99.992% 91.737%,99.967% 91.884%,99.926% 92.029%,99.869% 92.171%,99.797% 92.308%,99.71% 92.44%,99.609% 92.567%,99.494% 92.687%,99.365% 92.801%,99.223% 92.907%,89.82% 99.348%,89.82% 99.348%,89.683% 99.435%,89.538% 99.514%,89.387% 99.584%,89.229% 99.646%,89.066% 99.698%,88.899% 99.742%,88.727% 99.776%,88.552% 99.801%,88.374% 99.816%,88.194% 99.821%,2.404% 99.821%,2.404% 99.821%,2.014% 99.797%,1.644% 99.729%,1.299% 99.621%,0.984% 99.475%,0.704% 99.296%,0.464% 99.087%,0.268% 98.852%,0.123% 98.595%,0.031% 98.319%,0% 98.029%,0% 1.971% );
    padding: 1em 1.25em .875em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__title{
    font-size: 1em;
    line-height: 125%;
    letter-spacing: 0.02em;
  }
  .m-badge{
    margin-top: .625em;
  }
  &__img{
    margin-top: .1875em;
    height: 9em;
    width: 100%;
    @include center;
    img{
      max-height: 100%;
      max-width: 100%;
    }
  }
  &__props{
    width: 100%;
    margin-top: auto;
  }
}

.m-badge{
  display: inline-flex;
  align-items: center;
  padding: 0 .5em;
  background: linear-gradient(46deg, #d33236 0%, #b02b2b 0.01%, #d11216 100%);
  height: 1em;
  border-radius: .1875em;
  font-weight: 700;
  font-family: $font2;
  color: #fff;
  span{
    font-size: .5em;
    transform: translateY(.125em);
  }
  &._black-outline{
    background: transparent;
    border: 1px solid $black;
    color: $black;
  }
}

.product-prop{
  display: flex;
  justify-content: space-between;
  &:not(:last-child){
    border-bottom: 1px solid #e1e1e1;
  }
  &__title,
  &__desc{
    font-size: .6875em;
    line-height: 191%;
    letter-spacing: 0.02em;
  }
  &__desc{
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .collection{
    padding: 1.75em 0 2.375em;
    overflow: hidden;
    &__nav{
      margin-top: 2.1875em;
      .m-btn-gray-outline{
        display: none;
      }
    }
  }

  .collection-slider{
    margin-top: 1.75em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-slide{
        width: 15.25em;
      }
    }
  }

  ._hide-tablet{
    display: none;
  }
}


