.blog{
  padding: 2.625em 0 3.125em;
}

.blog-slider{
  margin-top: 2.5em;
  .swiper{
    &-container{
      width: calc(100% + 1.3125em);
    }
    &-slide{
      width: 20%;
      padding-right: 1.3125em;
    }
  }
}

.blog-card{
  clip-path: polygon( 0% 98.246%,0% 98.246%,0.03% 98.53%,0.117% 98.8%,0.256% 99.052%,0.443% 99.282%,0.672% 99.486%,0.939% 99.661%,1.24% 99.804%,1.569% 99.911%,1.922% 99.977%,2.294% 100%,97.706% 100%,97.706% 100%,98.078% 99.977%,98.431% 99.911%,98.76% 99.804%,99.061% 99.661%,99.328% 99.486%,99.557% 99.282%,99.744% 99.052%,99.883% 98.8%,99.97% 98.53%,100% 98.246%,100% 8.255%,100% 8.255%,99.992% 8.107%,99.967% 7.96%,99.927% 7.817%,99.872% 7.677%,99.801% 7.541%,99.716% 7.41%,99.617% 7.285%,99.504% 7.166%,99.378% 7.055%,99.239% 6.951%,89.784% 0.45%,89.784% 0.45%,89.654% 0.367%,89.518% 0.292%,89.375% 0.225%,89.226% 0.166%,89.072% 0.116%,88.914% 0.075%,88.752% 0.042%,88.588% 0.019%,88.42% 0.005%,88.251% 0%,2.294% 0%,2.294% 0%,1.922% 0.023%,1.569% 0.089%,1.24% 0.196%,0.939% 0.338%,0.672% 0.514%,0.443% 0.718%,0.256% 0.948%,0.117% 1.2%,0.03% 1.47%,0% 1.754%,0% 98.246% );
  background-color: $gray;
  padding: .875em;
  min-height: 17.8125em;
  border-radius: .3125em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  &__img{
    width: 100%;
    @include imgCover(59%);
    clip-path: polygon( 0% 100%,100% 100%,100% 15.458%,89.13% 0%,0% 0%,0% 100% );
    border-radius: .3125em;
    overflow: hidden;
    margin-bottom: 1.375em;
  }
  &__info{
    margin-top: .75em;
    @include grid100gap(.625em);
  }
  &__title{
    font-size: .875em;
    line-height: 121%;
    letter-spacing: 0.02em;
    font-weight: 600;
    @include truncate(2);
  }
  &__desc{
    font-size: .6875em;
    line-height: 136%;
    letter-spacing: 0.02em;
  }
}


@media screen and (max-width: 992px){
  .blog{
    padding: 2.375em 0 3.5em;
    overflow: hidden;
  }

  .blog-slider{
    margin-top: 1.6875em;
    .slider__wrap{
      overflow: unset;
    }
    .swiper-slide{
      width: 15em;
    }
  }
}


