.selection5{
  padding: 2.625em 0;
}

.leasing{
  padding: 8.75em 0 8.75em;
  margin-top: -3.5em;
  background-image: url(../img/leasing-decor2.svg);
  background-size: 100% auto;
  background-position: left bottom;
  background-repeat: no-repeat;
  &__inner{
    position: relative;
  }
  &__img{
    width: 41em;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  &__content{
    max-width: 31.75em;
  }
  &__subtitle,
  &__text{
    margin-top: 1.1875em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__list{
    margin-top: 2.25em;
    gap: 1em;
    span{
      color: $primary;
      font-weight: 700;
    }
  }
}

.achievement{
  margin-top: 2.625em;
  clip-path: polygon( 0% 17.924%,4.291% 0.084%,45.225% 0.084%,100% 0.084%,100% 81.927%,95.675% 99.766%,0% 99.766%,0% 17.924% );
  background-image: url(../img/achievement-bg2.webp);
  background-size: cover;
  border-radius: .5625em;
  padding: 3em 0;
  position: relative;
  z-index: 2;
  color: $white;
  .h2{
    text-align: center;
  }
  &__items{
    margin: 2em auto 0;
    width: 35em;
    max-width: 100%;
    display: flex;
    justify-content: space-between;
  }
}

.achievement-item{
  width: 15.625em;
  text-align: center;
  &__top{
    padding: 0 .5em;
    position: relative;
    margin-bottom: 1.25em;
    &-text{
      @include centerAbsolute;
      width: 100%;
      font-size: 1.3125em;
      line-height: 124%;
      font-family: $font2;
    }
  }
