      width: 100%;
      display: block;
    }
  }
  &__icon{
    position: absolute;
    top: 0;
    left: 0;
  }
  &__text{
    font-weight: 300;
  }
}


@media screen and (max-width: 992px){
  .selection5{
    padding: 2.375em 0;
  }

  .leasing{
    padding: 7.25em 0 5.75em;
    .h2{
      line-height: 180%;
    }
    &__subtitle,
    &__text{
      margin-top: .5em;
    }
    &__nav{
      margin-top: 1.875em;
    }
    &__img{
      position: static;
      max-width: 100%;
      display: block;
      transform: unset;
      margin: 1.25em 0;
    }
  }

  .achievement{
    margin-top: 2.375em;
    padding: 2em 0 3.625em;
    &__items{
      flex-wrap: wrap;
      justify-content: center;
      margin-top: .25em;
    }
  }

  .achievement-item{
    margin: 1.875em 1em 0;
  }
}


@media screen and (max-width: 576px){
  .leasing{
    background-image: url(../img/leasing-decor.svg);
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 100% auto;
    .h2{
      max-width: 80%;
    }
  }

