@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    width: 13.25em;
    height: 3em;
    font-weight: 700;
    font-family: $font2;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .625em;
    }
    &__icon{
        width: .9375em;
        margin-right: .3125em;
    }
}

.m-btn-red{
    clip-path: polygon( 5.862% 4.559%,5.862% 4.559%,5.983% 4.103%,6.111% 3.69%,6.245% 3.322%,6.384% 3%,6.528% 2.724%,6.676% 2.496%,6.828% 2.317%,6.983% 2.188%,7.14% 2.11%,7.299% 2.083%,97.642% 2.083%,97.642% 2.083%,97.986% 2.206%,98.312% 2.561%,98.617% 3.13%,98.895% 3.892%,99.142% 4.829%,99.355% 5.922%,99.527% 7.15%,99.656% 8.495%,99.736% 9.938%,99.764% 11.458%,99.764% 64.346%,99.764% 64.346%,99.756% 65.172%,99.731% 65.985%,99.691% 66.781%,99.636% 67.556%,99.566% 68.305%,99.481% 69.025%,99.382% 69.711%,99.27% 70.359%,99.145% 70.965%,99.007% 71.524%,92.487% 95.719%,92.487% 95.719%,92.37% 96.126%,92.246% 96.493%,92.118% 96.82%,91.985% 97.106%,91.848% 97.35%,91.708% 97.552%,91.565% 97.71%,91.419% 97.824%,91.271% 97.893%,91.122% 97.917%,2.358% 97.917%,2.358% 97.917%,2.014% 97.794%,1.688% 97.439%,1.383% 96.87%,1.105% 96.108%,0.858% 95.171%,0.645% 94.078%,0.473% 92.85%,0.344% 91.505%,0.264% 90.062%,0.236% 88.542%,0.236% 31.532%,0.236% 31.532%,0.243% 30.75%,0.265% 29.978%,0.301% 29.221%,0.351% 28.482%,0.415% 27.765%,0.491% 27.073%,0.581% 26.41%,0.682% 25.78%,0.796% 25.186%,0.921% 24.633%,5.862% 4.559% );
    background: $primary;
    color: $white;
    box-shadow: -.4375em .4375em 1.6875em 0 rgba(209, 18, 22, 0.21);
    overflow: hidden;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        background: linear-gradient(233deg, #d11216 0%, #6b090b 100%);
        transition: inherit;
        z-index: -1;
    }
    &:hover{
        color: $white;
        &:before{
            opacity: 1;
        }
    }
    &:active{
        background: #b02b2b;
        color: $white;
        &:before{
            opacity: 0;
        }
    }
}

.m-btn-gray-outline,
.m-btn-white-outline{
    background-color: transparent;
    background-size: 100% auto;
    background-repeat: no-repeat;
    position: relative;
