.footer{
  position: relative;
  color: $white;
  padding: 4.375em 0 1.125em;
  background: linear-gradient(229.12deg, rgb(72, 72, 72) 18.19%, rgb(209, 18, 22) 130.948%);
  clip-path: polygon( 69.376% 0%,100% 0%,100% 99.848%,56.736% 99.848%,0% 99.848%,0% 76.748%,0% 32.077%,0% 10.486%,63.832% 10.486%,63.832% 10.486%,63.856% 10.484%,63.879% 10.478%,63.902% 10.467%,63.926% 10.451%,63.949% 10.432%,63.972% 10.408%,63.994% 10.38%,64.017% 10.348%,64.039% 10.311%,64.061% 10.271%,69.147% 0.216%,69.147% 0.216%,69.169% 0.175%,69.191% 0.139%,69.214% 0.106%,69.236% 0.078%,69.259% 0.054%,69.282% 0.035%,69.306% 0.02%,69.329% 0.009%,69.352% 0.002%,69.376% 0% );
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__logos{
    display: flex;
    align-items: center;
    svg{
      width: 1.1875em;
      margin: 0 .75em;
    }
    img{
      width: 100%;
    }
    &-img{
      width: 12.25em;
      display: block;
    }
    &-img2{
      width: 7.25em;
    }
  }
  &__bottom{
    padding-top: 1em;
    margin-top: 2.25em;
    border-top: 1px solid rgba(255, 255, 255, 0.3);
    display: flex;
    justify-content: center;
  }
  &__c{
    opacity: .4;
    font-size: .8125em;
    line-height: 115%;
    letter-spacing: 0.02em;
  }
}

.footer-info{
  &__links{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    &-split{
      margin: 0 .625em;
      width: .4375em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 500;
    &:hover{
      color: $white;
      opacity: .8;
    }
    svg{
      width: 1.125em;
      margin-right: .5em;
    }
    span{
      font-size: .8125em;
      line-height: 115%;
      letter-spacing: 0.02em;
    }
  }
  &__nav{
    display: flex;
    margin-top: 1.75em;
  }
}

.footer-nav{
  &__title{
    font-size: .875em;
    line-height: 329%;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__links{
    @include grid100gap(.5em);
    a{
      font-size: .8125em;
      line-height: 115%;
      letter-spacing: 0.02em;
      &:hover{
        color: #c6a4a4;
      }
      &:active{
        color: $white;
      }
    }
  }
}

.scroll-top{
  position: absolute;
  width: 2.375em;
  @include center;
  right: 2.375em;
  bottom: 1.5em;
  @media screen and (max-width: 1380px){
    right: .25em;
  }
  svg{
    width: 100%;
  }
  &:hover{
    ._stroke{
      stroke: #c6a4a4;
    }
    ._fill{
      fill: #c6a4a4;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 3.5em 0 1.25em;
    &__top{
      font-size: .75em;
    }
    &__logos{
      &-img{
        width: 8.875em;
      }
      &-img2{
        width: 5.25em;
      }
      svg{
        width: .875em;
      }
    }
  }

  .scroll-top{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .footer{
    clip-path: polygon( 79.112% 0%,100% 0%,100% 100%,59.103% 100%,0% 100%,0% 36.806%,0% 14.126%,0% 3.164%,70.247% 3.164%,70.247% 3.164%,70.374% 3.161%,70.499% 3.154%,70.624% 3.143%,70.746% 3.127%,70.867% 3.107%,70.985% 3.082%,71.101% 3.053%,71.214% 3.02%,71.323% 2.983%,71.429% 2.941%,77.93% 0.222%,77.93% 0.222%,78.036% 0.181%,78.145% 0.144%,78.258% 0.11%,78.374% 0.082%,78.492% 0.057%,78.613% 0.037%,78.735% 0.021%,78.86% 0.009%,78.985% 0.002%,79.112% 0% );
    &__top{
      font-size: 1em;
      @include grid100gap(1.25em);
    }
  }

  .footer-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    &__links{
      margin-top: 1.125em;
    }
    &__nav{
      margin-top: 1.25em;
      width: 100%;
      .m-btn{
        width: 100%;
        height: 2.5em;
        background-image: url(../img/btn-stroke2.svg) !important;
        @include bgImgSize(100%);
      }
    }
  }
}
