.categories{
  padding: 2.625em 0;
  &__slider{
    margin-top: 2.75em;
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: 18.125em;
    }
    &-slide{
      width: 100%;
      padding-right: 1.5em;
      height: 24.25em;
      &:not(.swiper-slide-active){
        padding-top: 3.3125em;
        padding-bottom: 2em;
        padding-right: 5.25em;
        .category-card{
          padding: 1.75em .5em 1.25em 1.25em;
          &__img{
            height: 8em;
            opacity: .5;
            filter: grayscale(100%);
          }
          &__title{
            font-size: .75em;
          }
        }
        .product-card2__nav{
          padding-right: 1.25em;
        }
      }
      &-next+.swiper-slide{
        transform: translateX(-3.75em);
      }
      &-next+.swiper-slide+.swiper-slide{
        transform: translateX(-7.5em);
      }
      &-next+.swiper-slide+.swiper-slide+.swiper-slide{
        transform: translateX(-11.25em);
      }
      &-next+.swiper-slide+.swiper-slide+.swiper-slide+.swiper-slide{
        transform: translateX(-15em);
      }
    }
  }
}

.category-card{
  transition: all .3s;
  height: 100%;
  background-color: $gray;
  padding: 2.25em .25em 10em 1.75em;
  position: relative;
  clip-path: polygon(0 100%,100% 100%,100% 2.125em,calc(100% - 2.125em) 0,0 0,0 100%);
  border-radius: .5625em;
  overflow: hidden;
  &__title{
    font-weight: 600;
  }
  &__img{
    height: 10em;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    position: absolute;
    right: 0;
    bottom: 0;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .product-card2__nav{
    padding: 1.25em 2em 0 0;
    width: 100%;
    margin: 0;
  }
}

