@media screen and (max-width: 992px){
  .about2{
    padding: 2.375em 0;
    &__inner{
      padding-top: 0;
      min-height: unset;
    }
    &__content{
      width: 100%;
      padding: 0;
    }
    &__img{
      position: static;
      min-width: unset;
      width: calc(100% + 3.125em);
      margin-left: -1.5625em;
      margin-top: 1.875em;
      img{
        width: 100%;
        height: auto;
      }
    }
    &__top{
      gap: .6875em;
    }
    &__nav,
    .text{
      margin-top: 1.875em;
    }
  }

  .categories{
    padding: 2.375em 0;
    overflow: hidden;
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    .swiper-nav{
      margin-top: 1em;
    }
    &__slider{
      margin-top: 2.1875em;
      overflow: unset;
    }
  }

  .about-main{
    padding: 7.1875em 0 2.375em;
    &__content{
      max-width: 48%;
    }
    &__img{
      left: 50%;
      width: calc(50% + 1.5625em);
    }
    &__exclusive{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .about-main{
    &__content{
      max-width: 100%;
    }
    &__img{
      transform: translateX(0em);
      border-radius: 0;
      position: static;
      height: auto;
      width: calc(100% + 3.5625em);
      margin: 1em -.5625em 0 0;
      img{
        width: 100%;
        height: auto;
      }
    }
  }
}
