.main{
  background-color: $gray;
  padding: 10.625em 0 4.75em;
  background-image: url(../img/main-decor.svg);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: left bottom -2px;
  &__wrap{
    position: relative;
  }
  &__inner{
    display: grid;
    grid-template-columns: 28.75em 42.5em;
    justify-content: space-between;
    gap: .875em;
  }
  &__content{
    padding-top: 3em;
  }
  &__img{
    height: 23.75em;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__text{
    margin-top: 1.125em;
  }
  .catalog-item__nav{
    margin-top: 1.625em;
  }
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-nav{
      position: absolute;
      right: 0;
      bottom: -1.125em;
      width: 32.25em;
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 7.8125em 0 3.5em;
    margin-bottom: 1.75em;
    &__inner{
      grid-template-columns: repeat(2,calc(50% - .4375em));
      gap: .875em;
    }
    &__content{
      padding: 0;
    }
    &__text{
      margin-top: .8125em;
    }
    .catalog-item__nav{
      margin-top: .8125em;
    }
    &__img{
      height: 13.75em;
    }
    .swiper{
      &-nav{
        width: calc(50% - .4375em);
        bottom: unset;
        top: 100%;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    background-image: url(../img/main-decor2.svg);
    &__inner{
      grid-template-columns: 100%;
    }
    .swiper{
      &-nav{
        width: 16em;
        margin-top: 1.25em;
      }
    }
  }
}
