.breadcrumbs {
    li{
        position: relative;
        display: inline;
        color: #b0b0b0;
        text-transform: uppercase;
        &:not(:first-child) {
            &:before {
                content: '/ ';
            }
        }
    }
    li:before,
    a{
        font-size: .5625em;
        line-height: 278%;
    }
}

@media screen and (max-width: 992px){
    .breadcrumbs{
        ._hide-tablet{
            display: none;
        }
    }
}
