.fg {
  position: relative;
  margin: 0;
  &._required{
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: .375em;
      height: .375em;
      background-image: url(../img/star.svg);
      @include bgImgSize(100%);
    }
  }
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  select,
  input,
  textarea{
    font-family: $font1;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 0;
    border: none;
    padding: 0 2em;
    height: 100%;
    width: 100%;
    font-size: .6875em;
    color: $black;
    background-color: transparent;
    &::placeholder {
      color: $black;
    }
    &:disabled{
      pointer-events: none;
      color: rgba(#000,.4);
    }
  }
  select{
    background-image: url(../img/select-arrow.svg);
    background-repeat: no-repeat;
    background-position: right 1.36em center;
    background-size: 1.63em auto;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
  }
  &__field{
    position: relative;
    padding-bottom: 12.8%;
    background-image: url(../img/field-bg.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
  }
  &__icon{
    pointer-events: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.125em;
    height: 1.3125em;
    @include center;
    left: .875em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
    &~select,
    &~input,
    &~textarea{
      padding-left: 3.72em;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check{
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 1.4375em;
    min-height: 1em;
    span{
      font-size: .5625em;
      line-height: 140%;
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      pointer-events: none;
      top: 50%;
      transform: translateY(-50%);
      width: .9375em;
      height: .9375em;
      opacity: 0;
      transition: all .0s;
      @include bgImgSize(100%);
    }
    &:before{
      opacity: 1;
      background-image: url(../img/check.svg);
    }
    &:after{
      background-image: url(../img/check2.svg);
    }
    a{
      color: #b02b2b;
      font-weight: 600;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        opacity: 0;
      }
      &:after{
        opacity: 1;
      }
    }
  }
}



