.list-view{
  margin-top: 2.5em;
  &__items{
    @include grid100gap(.875em);
  }
}

.dealer-card2{
  padding: 1.5em 1.75em;
  border: 1px solid rgba(166, 166, 166, 0.80);
  border-radius: .75em;
  grid-template-columns: 20.5em 19em calc(100% - 51em - 6.75em) 11.5em;
  gap: 2.25em;
  display: none;
  &._show{
    display: grid;
    &:not(._show2){
      display: none;
    }
  }
  .dealer-card__props{
    margin: .75em 0 0;
    border: none;
    padding: 0;
  }
  .dealer-card__prop{
    img{
      width: 1.0625em;
      margin-right: .57em;
    }
    span{
      font-size: .75em;
    }
  }
  .dealer-card__time{
    font-size: .6875em;
    margin: 0;
  }
  .dealer-card__items{
    @include grid100gap(.75em);
    margin: 0;
  }
  .dealer-card__item{
    &-icon{
      width: 1em;
      height: 1em;
      margin-top: 0;
    }
    &-text{
      font-size: .6875em;
      line-height: 127%;
    }
    &._street{
      margin-top: .6875em;
    }
  }
  .dealer-card__btn{
    margin: 0;
  }
}


@media screen and (max-width: 992px){
  .dealer-card2{
    grid-template-columns: 100%;
    gap: 1em;
    padding: 1em;
    &__col3{
      margin-top: -.25em;
    }
    &__col4{
      max-width: 12em;
    }
  }
}


